enen las familias, mientras que, en el eje Y (vertical) se presentan los niveles de riesgo que pueden ser bajo, moderado o alto. Por tanto, una familia con un nivel de riesgo alto y con poca apropiación de sus capacidades para enfrentarlos (segmento azul oscuro) tendría una mayor probabilidad de amenaza y/o vulneración de derechos de niñas, niños y adolescentes y se ubicaría en el segmento superior izquierdo de la gráfica, mientras que una familia que tiene riesgo bajo y alto nivel de apropiación de capacidades, se ubicaría en la parte inferior derecha de la gráfica (segmento azul claro).")]),_vm._m(12),_c('TabsA',{staticClass:"color-acento-botones mb-5"},[_c('div',{staticClass:"tarjeta color-acento-botones--borde p-4",attrs:{"titulo":"Prevención primaria"}},[_c('h4',[_vm._v("Prevención primaria")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col"},[_c('p',{staticClass:"mb-0"},[_vm._v("Está dirigida a familias que no reportan ninguna dificultad específica o que presentan algunos conflictos entre sus miembros sin presencia de dificultades socioeconómicas, donde aún no se presentan riesgos frente a la protección, cuidado y promoción del desarrollo de los niños, niñas y adolescentes. Las actuaciones de los programas que incorporan este tipo de prevención están encaminadas a eliminar las causas que podrían generar vulneraciones a niñas, niños y adolescentes. Bajo este escenario se contemplan actuaciones dirigidas al fomento y defensa de los derechos de niñas, niños y adolescentes y a la consolidación de la familia como entorno protector (Kuehlein, 2009).")])]),_c('div',{staticClass:"col-lg-auto col-12 mb-4 d-none d-lg-block"},[_c('figure',[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/tema3/img09.svg"),"alt":""}})])])])]),_c('div',{staticClass:"tarjeta color-acento-botones--borde p-4",attrs:{"titulo":"Prevención secundaria"}},[_c('h4',[_vm._v("Prevención secundaria")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col"},[_c('p',{staticClass:"mb-0"},[_vm._v("Los programas de prevención secundaria están dirigidos a la población con alto riesgo de vulneración o con derechos vulnerados en estados muy tempranos. El objetivo de este tipo de programas es prevenir la materialización de las afectaciones a los derechos o disminuir sus efectos. Las intervenciones en este escenario deben estar dirigidas a entender la situación que genere el riesgo y realizar acompañamiento a las familias con el fin de promover mayor apropiación de sus recursos o capacidades para transformar la situación y desarrollar o fortalecer prácticas protectoras (Kuehlein, 2009).")])]),_c('div',{staticClass:"col-lg-auto col-12 mb-4 d-none d-lg-block"},[_c('figure',[_c('img',{staticClass:"img-t m-auto",attrs:{"src":require("@/assets/curso/tema3/10.svg"),"alt":""}})])])])]),_c('div',{staticClass:"tarjeta color-acento-botones--borde p-4",attrs:{"titulo":"Prevención terciaria"}},[_c('h4',[_vm._v("Prevención terciaria")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col"},[_c('p',[_vm._v("Finalmente, los programas de prevención terciaria tienen lugar en sujetos con derechos vulnerados o familias con múltiples problemas y poca apropiación de sus capacidades que no les permite activar autónomamente sus recursos. Estas familias enfrentan dificultades para ejercer de manera adecuada las funciones de protección y cuidado de sus integrantes; por ejemplo, familias con niñas, niños y adolescentes que están o estuvieron en PARD o SRPA.")]),_c('p',[_vm._v("El principal objetivo de este tipo de intervenciones es: “Generar un marco de actuación específico sobre aquellas situaciones posiblemente asociadas a la persistencia de una vulneración de derechos de los niños, niñas y adolescentes” (ICBF, s.f.); así como la restauración del goce efectivo de derechos, reducir las consecuencias de la vulneración y evitar nuevas afectaciones.")])]),_c('div',{staticClass:"col-lg-aut