ad sea un factor vulnerador en sí mismo (WHO, 2013), sino cuando este factor coincide con una situación de pobreza.")])]),_c('div',{attrs:{"titulo":"Estatus laboral"}},[_c('p',[_vm._v("Tanto el estatus laboral como el estado civil de los padres (solteros, separados o divorciado) son considerados como factores de alto riesgo, especialmente de negligencias, tal y como lo afirman Stith et al, 2009 citado en (WHO, 2013). Estos dos factores pueden originar estrés y baja autoestima y puede generar cuidado negligente con repercusiones en el desarrollo de niñas, niños y adolescentes. Estas consecuencias son especialmente más probables si el/la cuidador/a no cuenta con redes de apoyo familiares, de amistades o institucionales o es de escasos recursos económicos. Adicionalmente, el riesgo de que niñas, niños y adolescentes sean víctimas de amenaza o vulneraciones de derechos, aumenta en algunos casos, si está expuesto a la presencia de adultos que no son parte de la familia.")])]),_c('div',{attrs:{"titulo":"Consumo de sustancias"}},[_c('p',[_vm._v("Finalmente, se consideran como factores que aumentan el riesgo de que niñas, niños y adolescentes sean víctimas de violencia física o negligencia el que los padres o cuidadores consuman sustancias psicoactivas o alcohólicas, así como que hayan sido víctimas de violencia o negligencia en su infancia o que sufran de depresión, poca empatía o aislamiento social, (WHO, 2013).")])])])],1),_vm._m(17)]),_c('p',{staticClass:"mb-5"},[_vm._v("Aun cuando los estudios se concentran en las características de los padres o los cuidadores, el Informe Europeo de Prevención de Violencias en Contra de niñas, niños y adolescentes (2013) encuentra que los niños tienen un mayor riesgo de sufrir de violencia física mientras que las niñas enfrentan mayores riesgos de violencia sexual; además, niñas, niños y adolescentes que presentan problemas de conducta se encuentran en un mayor el riesgo de ser víctimas de violencia física (WHO, 2013).")]),_vm._m(18)])