e afectar de manera directa su desarrollo (WHO, 2013). En este mismo sentido, UNICEF (2018) afirma: “(…) la pobreza es también un factor de conflicto y la pobreza familiar puede influir en el funcionamiento y estabilidad de la familia, que puede contribuir además a un nivel inferior de salud mental y bienestar” (pág. 13).")]),_c('div',{staticClass:"bloque-texto-g bloque-texto-g--inverso bg-red-light p-3 p-sm-4 p-md-5 mb-5"},[_c('div',{staticClass:"bloque-texto-g__img",style:({'background-image': ("url(" + (require('@/assets/curso/tema3/19.svg')) + ")")})}),_vm._m(24)]),_c('p',{staticClass:"mb-5"},[_vm._v("La tabla que se presenta a continuación muestra de manera resumida los factores de riesgos asociados a cada nivel que aumentan la probabilidad de vulneración o amenaza de derechos de niñas, niños y adolescentes.")]),_vm._m(25),_vm._m(26)])],1)],1)}
